.actions {
    position: relative;
    margin-left: 20px;

    @media (max-width: 768px) {
        margin-left: 0;
    }

    &__list {
        position: absolute;
        left: 50%;
        bottom: 200%;
        transform: translate(-50%, 0);
        background: #fff;
        padding: 15px;
        box-shadow: 0px 4px 15px rgba(96, 128, 158, 0.64);
        border-radius: 10px;
        transition: all .2s linear;
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, -20px);

        @media (max-width: 993px) {
            left: unset;
            right: 0;
            transform: translate(0, 0);
        }

        &.active {
            opacity: 1;
            visibility: visible;
            transform: translate(-50%, 0);

            @media (max-width: 993px) {
                transform: translate(0, 0);
            }
        }
    }

    &__item {

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__btn,
    &__option {
        white-space: nowrap;
        display: flex;
        align-items: center;
        color: var(--gray);
        transition: all .2s linear;

        &:hover {
            color: #000;
        }
    }
}

.show__select-body {
    width: 60px;
}

.show__select {
    width: 60px;
}

.pagination {
    margin-right: 20px;
}

.show {
    margin-right: 20px;

    @media (max-width: 768px) {
        flex: 1 1 auto;
        margin-right: 0;
    }
}

.form__info-block--special {
    .form__select-header {
        padding-left: 15px;

        svg {
            margin-left: 10px;
        }

        &::before {
            display: none;
        }
    }

    .form__select {
        width: 85px;
    }

    .form__select-body {
        width: 85px;
    }

    .form__btn--special {
        width: 30px;
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        margin-left: 5px;
        position: relative;

        &.active {
            &::after {
                background: #FDC300;
                transform: translate(-50%, -130%);
            }
        }

        &::after {
            content: "";
            position: absolute;
            width: 13px;
            height: 13px;
            border-radius: 50%;
            left: 50%;
            transform: translate(-50%, 30%);
            top: 50%;
            background: #64B6AC;
            transition: all .2s linear;
        }
    }

    .form__btn--switcher {
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 75px;

        svg {
            fill: var(--green);
            transform: rotate(-90deg);
        }

        &.active {
            span {
                &:first-of-type {
                    display: inline;
                }
                &:last-of-type {
                    display: none;
                }
            }
        }

        span {

            &:first-of-type {
                display: none;
            }
        }
    }

    .form__info-block--wrapper {
        display: flex;
        align-items: center;
    }
}

.table__title {
    min-height: 20px;
}

.table {

    &.special {
        .table__special-price,
        .table__special-available {
            display: block;
        }

        .table__special {
            display: block;
            overflow: visible;
            width: 100%;
        }

        .table__special-profit {
            margin-right: 15px;
        }

        &.on {
            .table__special-item {
                &.mark {
                    &::after {
                        display: block;
                    }
                }
            }
        }
    }

    &.on {
        .table__special-profit {
            display: block;
        }

        .table__special {
            display: block;
            overflow: visible;
            width: 100%;
        }
    }

    &__special {
        padding-top: 34px;
        overflow: hidden;
        display: none;
        width: 0;
        max-width: fit-content;
    }

    &__special-list {
    }

    &__special-item {
        position: relative;
        font-size: 14px;
        color: var(--black);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 10px 23px 10px 15px;
        background-color: var(--white);
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px 0 0 20px;
        margin-bottom: 10px;
        height: 60px;
        margin-right: -25px;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 60px;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background: #FBDB72;
            display: none;
        }

        &:first-of-type {
            background-color: var(--light-green);
            height: 30px;
            margin-bottom: 16px;
            border-radius: 0;

            &::before {
                content: "";
                position: absolute;
                width: 30px;
                height: 30px;
                background-color: var(--light-green);
                left: -10px;
            }
        }

        &:not(:first-of-type) {

            .table__special-price {
                padding-left: 4px;
                pointer-events: none;
            }

            .table__special-available {
                padding-left: 7px;
                pointer-events: none;
            }
        }
    }

    &__special-price {
        position: relative;
        z-index: 1;
        margin-right: 5px;
        width: 35px;
        height: 100%;
        display: none;

        &:hover {
            .choose__name {
                cursor: unset;

                &::before {
                    background-color: var(--white);
                }
            }
        }
    }

    &__special-available {
        width: 40px;
        height: 100%;
        display: none;        

        &:hover {
            .choose__name {
                cursor: unset;

                &::before {
                    background-color: var(--white);
                }
            }
        }
    }

    &__special-profit {
        margin-right: 5px;
        margin-left: -15px;
        padding-left: 18px;
        height: 60px;
        width: 60px;
        display: none;
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;
    }

}
